import React from "react";
import clsx from "clsx";
import { AlertProps, AlertVariant } from "./Alert.types";
import "./Alert.scss";

const Alert: React.FC<AlertProps> = ({
  title,
  message,
  redirectLabel,
  redirectUrl,
  variant,
  className,
}) => {
  const styling = clsx({
    alert: true,
    "alert--info": variant === AlertVariant.INFO,
    "alert--muted": variant === AlertVariant.MUTED,
    "alert--warning": variant === AlertVariant.WARNING,
    "alert--error": variant === AlertVariant.ERROR,
  });

  return (
    <div className={`${styling} ${className}`}>
      {(variant === AlertVariant.WARNING || variant === AlertVariant.ERROR) && (
        <div style={{ borderRight: "2px solid yellow", minWidth: "40px" }}>
          <img
            src={
              variant === AlertVariant.WARNING
                ? "/icons/warning.svg"
                : "/icons/delete.svg"
            }
            width={32}
            height={32}
          />
        </div>
      )}
      <div>
        {title && <strong>{title}</strong>} {message && <p>{message}</p>}{" "}
        {redirectUrl && (
          <a href={redirectUrl} target="_blank" rel="noreferrer noopener">
            {redirectLabel ?? "Check it out"}
          </a>
        )}
      </div>
    </div>
  );
};

Alert.defaultProps = {
  title: "",
  message: "",
  redirectLabel: "",
  redirectUrl: "",
  /* @ts-ignore value in Enum */
  variant: "INFO",
  className: "",
};

export default Alert;
